@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hahmlet:wght@200&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  box-sizing: border-box; /*repect actual width of box by padding or border within the widths its been given, not in addition to it*/
  margin: 0;
  padding: 0;
  max-width: 100%; /* makes it so elements can never be bigger than their parents*/
}

/* general styles */
html {
  font-size: 62.5%; /*sets 1rem to 10px for font*/
  font-family: "Noto Sans JP", sans-serif;
  background: rgb(255, 255, 255);
  /* background: rgb(215, 234, 255); */
}
body {
  font-size: 1.6rem; /*sets font size back to 16px*/
  color: rgb(10, 42, 63);
  line-height: 1.5;
}
h1,
h2,
h3 {
  color: rgb(10, 42, 63);
}
h1 {
  font-size: 5rem;
}
h2 {
  font-size: 3.5rem;
}
a {
  text-decoration: none;
  color: #4f4f4f;
}

/* FILE FOR TOP (LEFT TITLE AND RIGHT FORM) */
/* top of site container */

.inputArea {
  display: flex;
  margin: 2% 3% 0% 3%;
  flex-flow: wrap;
  align-items: center;
  justify-content: center;
  font-family: "Poppins", sans-serif;
}

/* Title */
.left {
  flex: 1.5 1;
  display: block;
  width: 100%;
  text-align: center;
}

.left h2 {
  margin: -2% 0 0 0;
}

.left p {
  margin: 1% 15%;
  font-family: "Hahmlet", serif;
  font-weight: 600;
  font-size: 1.5rem;
}

h1 {
  font-family: "Poppins", sans-serif;
}

/* input fields and buttons (parent)*/
.right {
  flex: 1 1;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  padding: 1%;
  box-shadow: 3px 3px 9px;
  background-color: rgb(221, 211, 176);
}

/* label and field groupings (parent to task field and description field */
.parentField {
  text-align: center;
  padding: 1%;
  display: flex;
  flex-flow: column wrap;
}

/* task field only */
.taskField {
  font-size: 1.8rem;
  border-radius: 5px;
  border: solid rgb(10, 42, 63) 1px;
  padding: 4%;
}

/* description field only */
.descriptionField {
  font-size: 1.8rem;
  border-radius: 5px;
  border: solid rgb(10, 42, 63) 1px;
  padding: 4%;
  font-family: "Roboto", sans-serif;
}

.buttonParent {
  width: 100%;
  display: flex;
  justify-content: center;
}

.button {
  color: rgb(255, 255, 255);
  padding: 2% 0%;
  border-radius: 8px;
  border: none;
  width: 30%;
  margin: 0.5%;
  background: rgb(10, 42, 63);
  font-family: "Poppins", sans-serif;
}

button:hover {
  background: rgb(106, 137, 168);
}

/* --------------- */

@media (max-width: 800px) {
  .inputArea {
    flex-flow: column;
  }
  .left {
    text-align: center;
  }
  .button {
    width: 50%;
  }
}

@media (max-width: 800px) {
  .inputArea {
    flex-flow: row;
  }
}

@media (max-width: 600px) {
  .inputArea {
    flex-flow: column;
  }
  .right {
    margin: 3% 0;
    width: 95%;
  }
}

/* Parent for right&left tiles area */

.tile {
  display: flex;
  flex-flow: row;
  margin: 0;
  justify-content: center;
}

/* ------------------------------------------------ */
/* RIGHT side parent */

.rightBoxesParent {
  display: flex;
  flex-flow: column-reverse wrap;
  align-items: left;
  margin: 2% 1%;
  padding: 0%;
  width: 40%;
  align-self: flex-start;
  /* border: black solid 1px; */
}

/* 1st level child for email and account window area */

.account-window {
  border-radius: 3px;
  border: rgb(10, 42, 63) 1.5px solid;
  width: 95%;
  background: rgb(255, 255, 255);
  margin: 1% 1% 2% 1%;
  padding: 2% 6% 4% 6%;
  display: flex;
  flex-flow: column;
  align-items: center;
  /* border: rgb(10, 42, 63) 1px solid; */
}

.account-window h2 {
  width: 70%;
}

.account-window-buttons {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

.create-account-button {
  color: rgb(255, 255, 255);
  padding: 5% 0%;
  border-radius: 8px;
  border: none;
  width: 100%;
  margin: 1%;
  background: rgb(10, 42, 63);
  font-family: "Poppins", sans-serif;
}

.login-button {
  color: rgb(10, 42, 63);
  font-weight: 900;
  padding: 2% 0%;
  border-radius: 8px;
  border: none;
  width: 100%;
  margin: 1.5%;
  background: white;
  border: 1.7px solid rgb(10, 42, 63);
  font-family: "Poppins", sans-serif;
}

.login-button:hover {
  background-color: rgb(187, 214, 240);
}

/* NEWSLETTER */

.newsletter {
  border-radius: 3px;
  border: solid 6px rgb(221, 211, 176);
  width: 95%;
  background-color: white;
  margin: 1% 1% 2% 1%;
  padding: 2%;
  text-align: left;
  /* border: rgb(10, 42, 63) 1px solid; */
  display: flex;
  justify-content: center;
  text-align: center;
}

/* general h2 styling for suggestions header */
h2 {
  font-size: large;
  font-weight: 700;
  text-align: center;
  margin: 0 0 2% 0;
}

/* Newsletter input field */
.inputNewsletter {
  font-size: 1.8rem;
  border-radius: 5px;
  padding: 2% 3%;
  margin: 4% 0;
  width: 80%;
  border: solid rgb(10, 42, 63) 1px;
}

/* Newsletter submission button */
.buttonNewsletter {
  color: rgb(255, 255, 255);
  padding: 3% 0%;
  width: 30%;
  border-radius: 8px;
  border: none;
  margin: 0;
  background: rgb(10, 42, 63);
  font-family: "Poppins", sans-serif;
}

/* ---------------------------------------------- */

/* LEFT side parent of eachClientTile */
.aroundClientTiles {
  width: 50%;
  background-color: rgba(255, 255, 255, 0);
  margin: 0 1%;
  padding: 1%;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  color: white;
  font-size: large;
  align-self: flex-start;
}

/* Child under "aroundClientTiles" */
.eachClientTile {
  border-radius: 7px;
  width: 90%;
  background-color: rgb(10, 42, 63);
  margin: 1% 0;
  padding: 2%;
  text-align: center;
  display: flex;
  align-items: center;
  flex-flow: column;
}

/* MOBILE VIEWS */

@media (max-width: 800px) {
  .tile {
    flex-flow: column;
  }

  .aroundClientTiles {
    align-self: center;
  }

  .rightBoxesParent {
    align-items: center;
    align-self: center;
  }

  .rightBoxesParent {
    width: 95%;
  }
  .aroundClientTiles {
    width: 100%;
  }
  .eachClientTile {
    padding: 4% 6%;
  }
  .buttonNewsletter {
    width: 70%;
  }
}

@media (max-width: 600px) {
  .eachTile {
    width: 90%;
  }
}

/* parent */
.bar-area {
  width: 100%;
  padding: 0%;
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: 4% 0;
}

/* child */

.base-bar {
  padding: 0%;
  height: 100%;
  width: 90%;
  border-radius: 5px;
  background-color: #ff9065;
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
}

/* child of child */

.fill-bar {
  padding: 3%;
  background-color: #4cb955;
  border-right: 5px solid rgb(10, 42, 63);
  color: rgb(10, 42, 63);
  width: 20%;
  transition: all 0.8s ease-out;
}

/* ------------------ */

.second-base-bar {
  padding: 0%;
  height: 100%;
  width: 90%;
  border-radius: 5px;
  background-color: rgb(253, 234, 165);
  display: flex;
  justify-content: flex-start;
  margin: 1% 0 2% 0;
}

/* Label Area --------------- */

.labels {
  display: flex;
  flex-flow: column;
  text-align: left;
}

.label1 {
  color: #4cb955;
  font-size: 1.6rem;
  font-weight: 600;
}

.label2 {
  color: #ff9065;
  font-size: 1.6rem;
  font-weight: 600;
}

.label3 {
  color: rgb(253, 234, 165);
  font-size: 1.6rem;
  font-weight: 600;
  border-top: white solid 1px;
}

.user-format {
  font-family: "Poppins", sans-serif;
  color: white;
  font-size: x-large;
  letter-spacing: 0.15rem;
}

.num-owed {
  font-weight: 800;
  font-size: 200%;
  letter-spacing: 0.15rem;
}

.text-around-number {
  font-size: 1.6rem;
}

